@mixin transition-img {
    transition: transform 8s cubic-bezier(0.25, 0.46, 0.45, 0.94), -webkit-transform 8s cubic-bezier(0.25, 0.46, 0.45, 0.94);       
}
.section-image-gallery {
    .image__style {
        overflow: hidden; 
        img {
            @include transition-img;
        }
        &:hover {
            @media (hover: hover) {
                img {
                    transform: scale(1.2);
                }
            }
        }
    }
    .image-gallery_title { 
        margin-top: 16px;  
    }
    .image-gallery_subtext {
        margin-top: 6px; 
    }
}